<template>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px">
            <el-form-item label="" prop="storeName" >
                <el-input v-model="ruleForm.storeName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="storeType">
                <el-select v-model="ruleForm.storeType" filterable placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="companyName" >
                <el-input v-model="ruleForm.companyName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="storeAdress">
                <el-input v-model="ruleForm.storeAdress"></el-input>
            </el-form-item>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="joinTime">
                        <el-input v-model="ruleForm.joinTime" ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="legal">
                        <el-input v-model="ruleForm.legal"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="" prop="abbreviation">
                <el-input v-model="ruleForm.abbreviation" placeholder="50"></el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm('ruleForm')" type="primary"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
        </span>
    </el-dialog>
</template>

<script>
export default {
    name:'editorStore',
    props:['currentStoreInfo','title'],
    data(){
        return {
            dialogVisible:false,
            ruleForm: {
                address:'',//
                agency:'',//
                companyName:'',//
                bd:'',//bd
                abbreviation:'',//
                city:'',//
                cash:'',//
                code:'',//
                radio:'',//
                joinTime:'',//
                legal:'',//
                storeAdress:'',//
                storeName:'',//
                storeType:'',//
                tel:''//
            },
            // 
            rules: {
                companyName: [{ required: true, message: '', trigger: 'blur' }],
                abbreviation: [{ required: true, message: '', trigger: 'blur' }],
                city: [{ required: true, message: '', trigger: 'blur' }],
                cash: [{ required: true, message: '', trigger: 'blur' }],
                code: [{ required: true, message: '', trigger: 'blur' }],
                bd: [{ required: true, message: 'bd', trigger: 'blur' }],
                joinTime: [{ required: true, message: '', trigger: 'blur' }],
                plate:[{ required: true, message: '', trigger: 'blur' }],
                storeAdress:[{ required: true, message: '', trigger: 'blur' }],
                storeName:[{ required: true, message: '', trigger: 'blur' }],
                storeType:[{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    methods:{
        open() {
            if(this.title==''){
                this.ruleForm=Object.assign({},this.currentStoreInfo);
            }else{
                this.ruleForm={
                    address:'',//
                    agency:'',//
                    companyName:'',//
                    bd:'',//bd
                    abbreviation:'',//
                    city:'',//
                    cash:'',//
                    code:'',//
                    radio:'',//
                    joinTime:'',//
                    legal:'',//
                    tel:''//
                }
            }
            this.dialogVisible = true;
        },
        handleClose(done) {
            this.$confirm('')
            .then(_ => { 
                this.$refs.ruleForm.resetFields(); 
                done();
            })
            .catch(_ => {});
        },
        submitForm(formName) {
            console.log(this.ruleForm,111)
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.dialogVisible=false;
                    alert('');
                    if(this.title==''){
                        this.$emit('editSuccess',this.ruleForm);
                    }else{
                        this.$emit('addSuccess',this.ruleForm);
                    };
                    // ;
                    // this.$refs[formName].resetFields();
                } else {
                    alert('')
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.dialogVisible=false;
        },
        //   ;
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${ file.name }`);
        } 
    }
}
</script>

<style>

</style>




