<template>
    <div>
        <partner-info :key="index" v-for="(item, index) in items" :mode="mode" :item="item" @close="handleRemove(index)"></partner-info>
        <el-button v-show="mode === 'create' || mode === 'edit' && status === 'editing'" type="primary" class="full-row gap-2" @click="addPartner">+ </el-button>
    </div>
</template>
<script>
/**
 *  
 * 
 * TODO: 1. 
 *       2. mode
 */
    import PartnerInfo from './_PartnerInfo';
    // 
    export default {
        name: "partnerInfoes",
        components: {PartnerInfo},
        props: {
            items: Array,
            mode: {
                type: String,
                default: "view"
            }
        },
        data() {
            return {
               
            };
        },
        methods: {
            addPartner() {
                this.items.push({expand: true, name: ""});
            },
            handleRemove(index) {
                this.items.splice(index, 1);
            }
        },
        watch: {
            
        }
    }
</script>
<style scoped>

</style>


