<template>
    <el-container>
        <el-main>    
            <bread-crumb :items="breadCrumb"></bread-crumb> 
            <div style="text-align:right;">
                <el-button type="primary" @click="addStore">+</el-button>
            </div>      
            <el-form ref="form" :model="form" label-width="180px" class="gap-2">
                <el-row >
                    <el-col :span="12">
                        <el-form-item label="" prop="city">
                            <el-select v-model="form.city" placeholder="" filterable>
                                <el-option
                                    v-for="(item,index) in city"
                                    :key="index"
                                    :label="item"
                                    :value="item">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" prop="agent">
                            <el-input v-model="form.agent"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="storeType">
                            <el-select v-model="form.storeType" filterable>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                                <el-option label="" value=""></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" prop="storeName">
                            <el-input v-model="form.storeName"></el-input>
                        </el-form-item>
                        <el-row>
                            <el-col :span="5">
                                <el-form-item label="" prop="timeStart">
                                    <el-date-picker
                                        format="yyyy-MM-dd"
                                        v-model="form.timeStart"
                                        type="date"
                                        placeholder=""
                                        style="width:150px"
                                        value-format="yyyy-MM-dd">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                            <el-col :span="19">
                                <el-form-item prop="timeEnd" label="">
                                    <el-date-picker
                                        format="yyyy-MM-dd"
                                        v-model="form.timeEnd"
                                        type="date"
                                        placeholder=""
                                        style="width:150px"
                                        value-format="yyyy-MM-dd">
                                    </el-date-picker>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-form-item label="" prop="companyName">
                            <el-input v-model="form.companyName"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-row :gutter="20">
                <el-col :span="2" :offset="11">
                    <el-button class="reset" @click="resetForm('form')"></el-button>
                </el-col>
                <el-col :span="2">
                    <el-button type="primary" @click="search('form')"></el-button>
                </el-col>
            </el-row>
            <div class="search-result"> 95656900</div>
           <el-table :data="searInfoList" border style="width: 100%">
                <el-table-column prop="date" label="id" align="center" ></el-table-column>
                <el-table-column prop="name" label="" align="center" ></el-table-column>
                <el-table-column prop="address" label="" align="center" ></el-table-column>
                <el-table-column prop="address" label="" align="center" ></el-table-column>
                <el-table-column prop="address" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" align="center"></el-table-column>
                <el-table-column prop="name" label="" width="300px" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="editStore(scope.$index, scope.row)" type="text">|</el-button>
                        <el-button size="mini" @click="qrCode(scope.$index, scope.row)" type="text">|</el-button>
                        <el-button size="mini"  type="text" @click="firstDialogVisible = true,handleEnd(scope.$index, scope.row)"></el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="name" label="" align="center"></el-table-column>
            </el-table>
            <div class="block">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pagination.currentPage"
                    :page-sizes="[10, 2, 3, 400]"
                    :page-size="pagination.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pagination.total">
                </el-pagination>
            </div>
            <el-dialog title="" :visible.sync="firstDialogVisible" width="30%" >
                <p>1</p>
                <p>2</p>
                <p>3</p>
                <span slot="footer" class="dialog-footer">
                    <el-button type="primary" @click="firstDialogVisible = false,secondDialogVisible = true"></el-button>
                </span>
            </el-dialog>
            <el-dialog title="" :visible.sync="secondDialogVisible" width="30%" >
                <textarea name="" id="" rows="10" placeholder="" v-model="noJoin" style="width:100%;"></textarea>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="secondDialogVisible = false,continueJoin()"> </el-button>
                    <el-button type="primary" @click="secondDialogVisible = false,endJoin()" > </el-button>
                </span>
            </el-dialog>
            <!---->
            <editor-store ref="editor" v-if="isShow" :title="title" @editSuccess='editSuccess' @addSuccess='addSuccess' :currentStoreInfo="currentStoreInfo"></editor-store>
        </el-main>
    </el-container> 
</template>

<script>
import BreadCrumb from '@/components/common/BreadCrumb';
import EditorStore from '@/components/store/_EditorStore';

export default {
  name: 'StoreManagement',
  components:{BreadCrumb,EditorStore},
  data () {
    return {
        firstDialogVisible: false,//
        secondDialogVisible:false,//
        noJoin:'',//
        companyInfoIndex:'',//
        currentStoreInfo:'',//
        title:'',//
        isShow:false,
        // 
        form: {
            agent:'',//
            city:'',//
            companyName:'',//
            timeStart:'',//
            timeEnd:'',//
            storeName: '',//
            storeType:'',//
        },
        // 
        pagination:{
            currentPage:1,//1;
            pageSize:10,//10
            total:400//
        },
        breadCrumb: [{text:''},{text: ""}],
        city:['','','',''],
        tableData: [{
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-04',
          name: '',
          address: ' 1517 '
        }, {
          date: '2016-05-01',
          name: '',
          address: ' 1519 '
        }, {
          date: '2016-05-03',
          name: '',
          address: ' 1516 '
        }]
    }
  },
    methods:{
        // ;
        addSuccess(addInfo){
            // ;

            //
            this.tableData.unshift(addInfo);
            this.pagination.currentPage=1;
            this.pagination.pageSize=10;
            this.pagination.total++;
        },
        // ;
        editSuccess(editInfo){
            // ;
            this.tableData.splice(this.companyInfoIndex,1,editInfo);
        },
        //form   
        formInfo(){

        },
        resetForm(formName) {
            this.$refs.form.resetFields();
        },
        //   
        reset(){
            this.form= {
                agent:'',//
                business:'',//
                city:'',//
                companyName:'',//
                timeStart:'',//
                timeEnd:'',//
                storeName: '',//
                storeAddress:'',//
                timeOver:''//
            }
        },
        //
        search(val){
            console.log(val,1111111)
            console.log(this.form);
        },
        //
        handleSizeChange(val) {
            this.pagination.pageSize=val;
        },
        //
        handleCurrentChange(val) {
            this.pagination.currentPage=val;
        },
        // 
        addStore(){
            //  
            this.title='';
            this.isShow=true;
            setTimeout(()=>{
                this.$refs.editor.open();
            },200); 
        },
        //
        editStore(index, row){
            //;
            this.companyInfoIndex=(this.pagination.currentPage-1)*this.pagination.pageSize+index;
            // ;
            this.currentStoreInfo=this.tableData[this.companyInfoIndex]; 
            this.title='';
            this.isShow=true;//;
            // ,setTimeout;
            setTimeout(()=>{
                this.$refs.editor.open();
            },200);  
        },
        //
        qrCode(index, row){
            this.$refs.commission.open();
        },
        //,
        handleEnd(index,row){
        },
        //,
        endJoin(){
            //;
        },
        //
        continueJoin(){
            this.noJoin='';
        }
    },
    computed:{
        //
        searInfoList(){
            return this.tableData.slice((this.pagination.currentPage-1)*this.pagination.pageSize,this.pagination.currentPage*this.pagination.pageSize);
        }
    }
}
</script>

<style>
    .search-result{
        color:gray;
        margin-bottom: 20px;
        margin-top: 50px;
    }
    .block{
        margin-top: 100px;
        text-align: center;
    }
    .el-button{
        margin-left: 0 !important;
    }
</style>





