<template>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
        <el-form :model="ruleForm" :rules="rules"  ref="ruleForm" label-width="110px" class="demo-ruleForm">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="bankType" class="tl">
                        <el-input v-model="ruleForm.bankType"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="subBranch">
                        <el-input v-model="ruleForm.subBranch"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="name" class="tl">
                        <el-input v-model="ruleForm.name"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="account">
                        <el-input v-model="ruleForm.account"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
            <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm('ruleForm')" type="primary"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
        </span> 
    </el-dialog>
</template>

<script>
export default {
    data(){
        return {
            dialogVisible:false,
            ruleForm:{
                name: '',//
                subBranch: '',//
                account:'',//
                a:'',
                bankType:'',//  
            },
            rules: {
                bankType:[{ required: true, message: '', trigger: 'blur' }],
                subBranch: [{ required: true, message: '', trigger: 'blur' }],
                name: [{ required: true, message: '', trigger: 'blur' },],
                account:[{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    methods:{
        submitForm(formName) {
            console.log(this.ruleForm,234)
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.dialogVisible=false;
                    alert('');
                    this.$refs[formName].resetFields();
                } else {
                    alert('')
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.dialogVisible=false;
        },
        handleClose(done) {
            // x;
            this.$confirm('')
            .then(_ => { 
                // 
                this.$refs.ruleForm.resetFields();
                done();
            })
            .catch(_ => {
                //
            });
        },
        // ;
        open(){
            this.dialogVisible=true;
        }
    }
}
</script>

<style scoped>
    .el-dialog__footer{
        text-align: left;
    }
</style>





