<template>
    <CollapsePanel class="gap-2" :expand="expand">    
        <div align-left slot="header" class="clearfix">
            <span style="margin-right: 30px"></span>     
            <el-button v-show="mode === 'edit' && status !== 'editing'" @click="handleEdit" type="primary" size="mini"></el-button>         
            <el-button v-show="mode === 'edit' && status === 'editing'" @click="handleCancel" type="danger" size="mini"></el-button>         
            <el-button v-show="mode === 'edit' && status === 'editing'" @click="handleComplete" type="success" size="mini"></el-button>                                   
        </div>
        <el-form :model="item" label-width= "180px" v-show="mode === 'create' || mode === 'edit' && status === 'editing'">
            <el-form-item label="">
                <el-switch
                    v-model="item.signed"
                    active-color="#13ce66"
                    inactive-color="#aaa">
                </el-switch>
            </el-form-item>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="item.name"></el-input>
                    </el-form-item>                                                                         
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="item.shortName"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>   
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="item.num"></el-input>
                    </el-form-item>                                                                         
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="item.code"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>     
            <el-form-item label="">
                <el-input v-model="item.address"></el-input>
            </el-form-item> 
            <el-form-item label="">                
                    <upload v-if="mode === 'create' || mode === 'edit' && status === 'editing'"></upload>
            </el-form-item>  

            <!--  -->
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-date-picker
                            v-model="item.finishDate"
                            type="date"
                            placeholder="">
                        </el-date-picker>
                    </el-form-item>                                                                         
                </el-col>               
            </el-row>  
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="item.tracerName"></el-input>
                    </el-form-item>                                                                         
                </el-col>               
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="item.mobile"></el-input>
                    </el-form-item>                                                                         
                </el-col>
            </el-row>     
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="item.email"></el-input>
                    </el-form-item>                                                                         
                </el-col>               
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="item.idNum"></el-input>
                    </el-form-item>                                                                         
                </el-col>
            </el-row>  
            <el-form-item label="">
                <el-input type="textarea" v-model="item.remark"></el-input>
            </el-form-item> 

            <el-form-item label="">                
                    <upload v-if="mode === 'create' || mode === 'edit' && status === 'editing'"></upload>
            </el-form-item>
            <el-form-item label="">
                <upload v-if="mode === 'create' || mode === 'edit' && status === 'editing'"></upload>
            </el-form-item>
        </el-form>

        <el-form :model="item" label-width= "180px" v-show="mode === 'view' || mode === 'edit' && status === ''">
            <el-form-item label="">
                {{item.signed&&"" || ""}}                
            </el-form-item>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        {{item.name}}
                    </el-form-item>                                                                         
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        {{item.shortName}}
                    </el-form-item>
                </el-col>
            </el-row>   
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        {{item.num}}
                    </el-form-item>                                                                         
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        {{item.code}}
                    </el-form-item>
                </el-col>
            </el-row>     
            <el-form-item label="">
                {{item.address}}
            </el-form-item> 
            <el-form-item label="">                
                    <file-list></file-list>
            </el-form-item>  

            <!--  -->
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        {{item.finishDate}}
                    </el-form-item>                                                                         
                </el-col>               
            </el-row>  
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        {{item.tracer}}
                    </el-form-item>                                                                         
                </el-col>               
                <el-col :span="12">
                    <el-form-item label="">
                        {{item.mobile}}
                    </el-form-item>                                                                         
                </el-col>
            </el-row>     
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        {{item.email}}
                    </el-form-item>                                                                         
                </el-col>               
                <el-col :span="12">
                    <el-form-item label="">
                        {{item.idNum}}
                    </el-form-item>                                                                         
                </el-col>
            </el-row>  
            <el-form-item label="">
                {{item.remark}}
            </el-form-item> 

            <el-form-item label="">                
                    <file-list></file-list>
            </el-form-item>
            <el-form-item label="">
                <file-list></file-list>
            </el-form-item>
        </el-form>
    </CollapsePanel>
</template>
<script>
import CollapsePanel from '@/components/common/CollapsePanel';
import Upload from '@/components/common/Upload';
import FileList from '@/components/common/FileList';
/**
 * 
 * TODO: 
 *  1. 
 *  2. 
 */
export default {
    name: "agentCompanyInfo",
    props: {
            item: Object,
            mode: {
                type: String,
                default: "view"
            }
        },
    components: {CollapsePanel, Upload, FileList},
    data() {
        return {
            expand: true,
            status: "",
            originalItem: {

            },
        };
    },
    methods: {
        handleEdit() {
                this.status = 'editing';
                this.originalItem = this.item;
                this.item = JSON.parse(JSON.stringify(this.item || {}));          
            },
            handleComplete() {
                this.status = '';                                
            },
            handleCancel() {
                this.status = '';
                this.item = this.originalItem;                
            }
    }
}
</script>
<style scoped>
.el-date-editor {
    width: 100%;
}
</style>


