<template>
    <el-dialog :title="title" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="110px">
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="companyName" class="tl">
                        <el-input v-model="ruleForm.companyName" placeholder="50"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="abbreviation">
                        <el-input v-model="ruleForm.abbreviation" placeholder="50"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="city" class="tl">
                        <el-input v-model="ruleForm.city" placeholder="..."></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="cash">
                        <el-input v-model="ruleForm.cash"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="code" class="tl">
                        <el-input v-model="ruleForm.code" placeholder="50"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item prop="radio">
                        <el-radio-group v-model="ruleForm.radio">
                            <el-radio label="" value=""></el-radio>
                            <el-radio label="" value=""></el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="bd" prop="bd"  label-width="40px" class="tl">
                <el-input v-model="ruleForm.bd" placeholder=""></el-input>
            </el-form-item>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="joinTime" class="tl">
                        <el-input v-model="ruleForm.joinTime" ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="legal">
                        <el-input v-model="ruleForm.legal"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" class="tl" prop="tel">
                        <el-input v-model="ruleForm.tel" ></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="address" >
                        <el-input v-model="ruleForm.address"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="" class="tl" prop="agency">
                <el-select v-model="ruleForm.agency" filterable>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item  class="tl upload">
                <el-upload
                class="upload-demo"
                action="https://jsonplaceholder.typicode.com/posts/"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :before-remove="beforeRemove"
                multiple
                :limit="3"
                :on-exceed="handleExceed">
                    <el-button size="small" type="primary"></el-button>
                    <div slot="tip" class="el-upload__tip">(txt,doc,docx,xls,xlsx,ppt,pptx,pdf,jpg,jpeg,png,gif,rar,zip)</div>
                </el-upload>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="submitForm('ruleForm')" type="primary"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
        </span>
    </el-dialog>
</template>

<script>
export default {
    name:'editorCompany',
    props:['currentCompanyInfo','title'],
    data(){
        return {
            dialogVisible:false,
            ruleForm: {
                address:'',//
                agency:'',//
                companyName:'',//
                bd:'',//bd
                abbreviation:'',//
                city:'',//
                cash:'',//
                code:'',//
                radio:'',//
                joinTime:'',//
                legal:'',//
                tel:''//
            },
            // 
            rules: {
                companyName: [{ required: true, message: '', trigger: 'blur' }],
                abbreviation: [{ required: true, message: '', trigger: 'blur' }],
                city: [{ required: true, message: '', trigger: 'blur' }],
                cash: [{ required: true, message: '', trigger: 'blur' }],
                code: [{ required: true, message: '', trigger: 'blur' }],
                bd: [{ required: true, message: 'bd', trigger: 'blur' }],
                joinTime: [{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    methods:{
        open() {
            if(this.title==''){
                this.ruleForm=Object.assign({agency:''},this.currentCompanyInfo);
            }else{
                this.ruleForm={
                    address:'',//
                    agency:'',//
                    companyName:'',//
                    bd:'',//bd
                    abbreviation:'',//
                    city:'',//
                    cash:'',//
                    code:'',//
                    radio:'',//
                    joinTime:'',//
                    legal:'',//
                    tel:''//
                }
            }
            this.dialogVisible = true;
        },
        handleClose(done) {
            this.$confirm('')
            .then(_ => { 
                this.$refs.ruleForm.resetFields(); 
                done();
            })
            .catch(_ => {});
        },
        submitForm(formName) {
            console.log(this.ruleForm,111)
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.dialogVisible=false;
                    alert('');
                    if(this.title==''){
                        this.$emit('editSuccess',this.ruleForm);
                    }else{
                        this.$emit('addSuccess',this.ruleForm);
                    };
                    // ;
                    // this.$refs[formName].resetFields();
                } else {
                    alert('')
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
            this.dialogVisible=false;
        },
        //   ;
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        handleExceed(files, fileList) {
            this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
        },
        beforeRemove(file, fileList) {
            return this.$confirm(` ${ file.name }`);
        } 
    }
}
</script>

<style>
    .tl .el-form-item__label{
        text-align: left;
    }
    .upload .el-form-item__content{
        margin-left: 0!important;
    }
</style>




